import React from 'react';
import { Row, Col} from 'reactstrap';

const Title = () => {
    return (
      <Row>
        <Col xs="6" sm="4"></Col>
         <Col xs="6" sm="4"><h1>Guess Da number</h1></Col>
        <Col xs="6" sm="4"></Col>
        </Row>
       );
}

export default Title;



