import React, { Component } from 'react';
import { Container, Row, Col, Input, Button } from 'reactstrap';
import Title from './components/title';
import SubTitle from './components/subtitle';
import GetInput from './components/getinput';
import Result from './components/result';


class App extends Component {
  constructor(props){
  super(props);
  
  this.state = {
    counter: 0,
    userNr: '',
    randomNr: '',
    isEnd: false,
    text: ''
    }
  }

  componentDidMount(){ this.setState({ randomNr: Math.floor(Math.random() * 11) }); }

  handleChange = e => {
    e.preventDefault();
    this.setState({ userNr: parseInt(e.target.value.replace(/[^0-9.]+/g, "")) });
  }

 letsPlay = () => {  
   if (this.state.userNr) {
     this.setState( prevState => ({ 
         isEnd: !prevState.isEnd,
         counter: prevState.counter + 1, 
         text: prevState.userNr === prevState.randomNr ? 'Congratulations, you win the game!' :  `Oh snap, you lost the game, because the number is ${this.state.randomNr}` 
      })
    );
   } else {
    this.setState({ text: 'Please fill the form before you play!'});
  }
}

  newGame = () => {
    this.setState({
      counter: 0,
      userNr: '',
      randomNr: Math.floor(Math.random() * 11),
      isEnd: false,
      text: '',
    });
  }

  render() {
    return (
    <Container>
      <Title/>
       <SubTitle /> 
        <GetInput type="number" handleInput={this.handleChange} input={this.state.userNr}/>
       <Row>
         <Col xs="6" sm="4"></Col>
         <Col xs="6" sm="4">
         { !this.state.isEnd 
          ? <Button color="primary" onClick={this.letsPlay} size="lg" block>Let's play</Button>
          : <Button color="success" onClick={this.newGame} size="lg" block>New Game</Button>
           }
           </Col>
         <Col xs="6" sm="4"></Col>
       </Row>
       <Result text={this.state.text}/>
    </Container>
    );
  }
}

export default App;


