import React from 'react';
import { Row, Col, Input }  from 'reactstrap';

const GetInput = (props) => {
    return (
      <Row>
        <Col xs="6" sm="4"></Col>
         <Col xs="6" sm="4"><Input type="this.props.number" onChange={props.handleInput} size="lg" value={props.input}/></Col>
        <Col xs="6" sm="4"></Col>
        </Row>
       );
}


export default GetInput;



