import React from 'react';
import { Row, Col} from 'reactstrap';

const SubTitle = (props) => {
  return (
      <Row>
        <Col xs="6" sm="4"></Col>
         <Col xs="6" sm="4">Iam thinking about a number between 0 and 10! </Col>
        <Col xs="6" sm="4"></Col>
        </Row>
        );
}

export default SubTitle;



