import React from 'react';
import { Row, Col} from 'reactstrap';

const Result = (props) => {
  return (
      <Row>
        <Col xs="6" sm="4"></Col>
         <Col xs="6" sm="4">{props.text}</Col>
        <Col xs="6" sm="4"></Col>
        </Row>
        );
}

export default Result;



